@use "./mixins" as *;
@use "./variables" as *;
@use "./dessert" as *;

.desserts {
  flex: 1;
  h1 {
    color: $text-color;
    margin-bottom: 20px;
    font-size: 50px;
  }
  .content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 30px;
  }
}
